import React, {useEffect, useState} from 'react';
import '../css/App.scss';
import Navigation from './Navigation';
import ToDosContainer from './ToDosContainer';
import ToDonesContainer from './ToDonesContainer';
import Help from '../views/Help'
import {v4 as uuid} from 'uuid'
import {HashRouter, Route, Switch} from 'react-router-dom'
import NotFound from '../views/NotFound';

const App = () => {

  const [tasks, setTasks] = useState(
    [
      {id:uuid(), text: 'Make a website', done: true },
      {id:uuid(), text: 'Call my mom', done: true },
      {id:uuid(), text: 'Finish reading my book', done: true },
      {id:uuid(), text: 'Make more moneys', done: true },
      {id:uuid(), text: 'Wash my face!', done: false },
      {id:uuid(), text: 'Walk the dog', done: false },
      {id:uuid(), text: 'Pay the rent', done: false },
      {id:uuid(), text: 'Make so moneys', done: false }
    ]
  ) 

  //to execute on load when the application is just opened for the first time:
  // the same as ComponentDidMount
  useEffect(()=>{
    // let data = JSON.parse(localStorage.getItem('todoItems'))
    // if (data.length>0) {
    //   setTasks(data)
    // }

    //ANOTHER WAY: 
    let data = localStorage.getItem('todoItems')
    if (data) {
      let parsedData = JSON.parse(data)
      setTasks(parsedData)
    }
  }, [])
  
  const todos = tasks.filter(item => !item.done)
  const todones = tasks.filter(item => item.done)

  const DeleteItem = (id) => {
    const updated = tasks.filter(task => task.id !== id)
    localStorage.setItem('todoItems', JSON.stringify(updated))
    setTasks(updated)
  }

  const AddItem =(text) => {
    let task = {id: uuid(), text: text, done: false}
    //local storage is permanent
    localStorage.setItem('todoItems', JSON.stringify([task, ...tasks]))

    //session is only for one session until the tab is closed
    sessionStorage.setItem('todoItems', JSON.stringify([task, ...tasks]))

    setTasks([task, ...tasks])
  }

  const UpdateItem = (id) => {
    let updatedTasks = tasks.map(task => {
      if (task.id === id) {
        // change from false to true and vice versa: 
        task.done = !task.done
      }
      return task;
    })
    localStorage.setItem('todoItems', JSON.stringify([updatedTasks, ...tasks]))

    setTasks(updatedTasks)

 //// SHORTER WAY WITH TERNARY OPERATOR:
    // let updatedTasks = tasks.map(item=> item.id === id ? 
    //   {...item, done:!item.done} : item)
    //   setTasks(updatedTasks)
  }

  
 

  return (
    <HashRouter>
      <div className="app">
        <Navigation></Navigation>
      {/* Switch will return only a single component */}
        <Switch>
          <Route path="/">
              <ToDosContainer
                AddItem={AddItem}
                todos={todos}
                UpdateItem={UpdateItem}>

              </ToDosContainer>

              <ToDonesContainer
                DeleteItem={DeleteItem} 
                todones={todones}
                UpdateItem={UpdateItem}>
              </ToDonesContainer>
          </Route>

          <Route path="/help">
            <Help/>
          </Route>

          <Route>
            <NotFound/>
          </Route>
          
        </Switch>
       
      </div>
    </HashRouter>
  );
};

export default App;
